export default {
	data() {
		return {
			
		}
	},
	methods: {
		handleGetUserInfo(res){
			console.log(res.detail.userInfo)
			if(res.detail.userInfo){ // 允许
				// 1. 将用户数据存值本地
				uni.setStorageSync('userInfo', JSON.stringify(res.detail.userInfo))
				// 2. 跳转至个 人中心页
				uni.reLaunch({
					url: '/pages/personal/personal'
				})
			}else { // 拒绝
				uni.showToast({
					title: '授权失败',
					icon: 'none'
				})
			}
		}
	}
}
